import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    aboutOn : false,
    workOn : false
  },
  mutations: {
    aboutToggle () {
      this.state.aboutOn = !this.state.aboutOn;
    },
    aboutOff () {
      this.state.aboutOn = false;
    },
    turnWorkOn () {
      this.state.workOn = true;
    },
    turnWorkOff () {
      this.state.workOn = false;
    }
  },
  actions: {
  },
  modules: {
  }
})
