            indiceWork: 0
        }
    },
    methods: {
        ...mapMutations(['turnWorkOn','turnWorkOff']), 
        incrementI: function () {
            console.log('hola...')
            if ( this.indiceWork < 2 ) {
                this.indiceWork = this.indiceWork + 1;
            }
            console.log(this.indiceWork);
        },
        decrementI: function () {
            if ( this.indiceWork > 0 ) {
                this.indiceWork = this.indiceWork - 1;
            }
        }
    }
}
</script>