<template>
  <div id="app" :class="{hideOn:aboutOn, hideOn2:workOn}">
    <!-- main nav -->
    <header class="navegador">
      <a class="main-link" @click="changeIndice(0)" v-if="!menuActive">Emens Fede</a>
      <a class="main-link" @click="toggleMenu(); changeIndice(0)" v-if="menuActive">Emens Fede</a>
      <nav>
        <div class="icons" >
          <a href="https://www.linkedin.com/in/emens-federico/" target="blank" class="linkedin"><img src="@/assets/img/linkedin.svg" alt=""></a>
          <a href="https://github.com/fedeemens" target="blank" class="github">GitHub</a>
        </div>
        <div class="burguer" @click="toggleMenu()" :class="{'cruz': menuActive}">
          <div class="line1"></div>
          <div class="line2"></div>
        </div>
      </nav>
      <div class="menu" :class="{'menu-active': menuActive}">
        <p @click="toggleMenu(); changeIndice(0)">HOME</p>
        <p @click="toggleMenu(); changeIndice(1)">ABOUT ME</p>
        <p @click="toggleMenu(); changeIndice(2)">WORK</p>
        <p @click="toggleMenu(); changeIndice(3)">CONTACT</p>
      </div>
    </header>

    <!-- Side nav -->
    <div class="navegador-lateral">
      <div class="line-container" @click="changeIndice(0)">
        <div class="line1" :class="{active : (this.indice == 0)}"></div>
      </div>
      <div class="line-container" @click="changeIndice(1)">
        <div class="line2" :class="{active : (this.indice == 1)}"></div>
      </div>
      <div class="line-container" @click="changeIndice(2)">
        <div class="line3" :class="{active : (this.indice == 2)}"></div>
      </div>
      <div class="line-container" @click="changeIndice(3)">
        <div class="line4" :class="{active : (this.indice == 3)}"></div>
      </div>
    </div>

    <!-- main app -->
    <div class="container-visible" @mousewheel="onScroll" :class="{overflowOn: aboutOn, overflowOn2: workOn}">
      <div class="container-primary" :style="{ top: '-'+(indice * 100)+'vh' }" @transitionend="transitionEnd">
        <Section1 :class="{active : (this.indice==0)}"/>
        <Section2 :class="{active : (this.indice==1)}" />
        <Section3 :class="{active : (this.indice==2)}" v-if="!aboutOn"/>
        <Section4 :class="{active : (this.indice==3)}" v-if="!aboutOn && !workOn"/>
      </div>
    </div>

  </div>
</template>

<script>
import Section1 from './components/Section1.vue'
import Section2 from './components/Section2.vue'
import Section3 from './components/Section3.vue'
import Section4 from './components/Section4.vue'
import {mapState} from 'vuex';

export default {
  name: 'App',
  components: {
    Section1,
    Section2,
    Section3,
    Section4,
  },
  computed: {
    ...mapState(['aboutOn','workOn'])
  },
  data() {
    return {
      indice: 0,
      transitionOn: false,
      menuActive: false
    }
  },
  methods: {
    onScroll: function (e) {
      if ((e.deltaY == 100) && (this.indice < 3) && (!this.transitionOn) && (!this.aboutOn) && (!this.workOn)) {
        this.transitionOn = true;
        this.indice = this.indice + 1;
      }
      if ((e.deltaY == -100) && (this.indice != 0) && (!this.transitionOn) && (!this.aboutOn) && (!this.workOn)){
        this.transitionOn = true;
        this.indice = this.indice - 1;
      }
    },
    changeIndice: function (number) {
      this.indice = number;
    },
    transitionEnd: function () {
      this.transitionOn = false;
    },
    toggleMenu: function() {
      this.menuActive = !this.menuActive;
    }
  },
  mounted() {
    document.addEventListener('keydown',(e)=>{
      if ( (e.key == 'ArrowDown') && (this.indice < 3) && (!this.transitionOn) && (!this.aboutOn) && (!this.workOn)){
        this.transitionOn = true;
        this.indice = this.indice + 1;
      }
      if ((e.key == 'ArrowUp') && (this.indice != 0) && (!this.transitionOn) && (!this.aboutOn) && (!this.workOn)){
        this.transitionOn = true;
        this.indice = this.indice - 1;
      }
    }) ;
  }
}
</script>

<style lang="scss">
