          margin-bottom: 16px;
        }
      }
      .cruz {
        .line1 {
          transform: rotate(45deg) translate(7px,7px);
        }
        .line2 {
          transform: rotate(-45deg) translate(7px,-5px);
        }
      }
    }
    .menu {
      position: fixed;
      height: 100vh;
      width: 100vw;
      z-index: -1;
      display: flex;
      justify-content: space-around;
      padding: 5% 0;
      align-items: center;
      flex-direction: column;
      background: linear-gradient(to right, #072142, #8c2b7a 42%, #ff4d5a);
      left: 0;
      top:0;
      transform: translateY(-100%);
      transition: all 0.4s;
      p {
        color: #fff;
        font-size: 8rem;
        line-height: 1;
        letter-spacing: 0.1em;
        font-family: "Source Sans Pro", sans-serif;
        cursor: pointer;
        opacity: 0;
        transition: all 0.4s;
        position: relative;
      }
      p:after {
        content: '';
        top: 50%;
        transform: translateY(-50%);
        left: 49%;
        height: 12px;
        position: absolute;
        width: 0%;
        background: #ffffff;
        transition: all 0.2s ease-in-out;
        border-radius: 5px;
      }
      p:before {
        content: '';
        top: 50%;
        transform: translateY(-50%);
        right: 49%;
        height: 12px;
        position: absolute;
        width: 0%;
        background: #ffffff;
        transition: all 0.2s ease-in-out;
        border-radius: 5px;
      }
      p:hover:after,p:hover:before {
        width: 51%;
      }
    }
    .menu-active {
      transform: translateY(0%);
      p {
        transition-delay: 0.4s;
        opacity: 1;
      }
    }
  }
  .navegador-lateral {
