import Vue from 'vue'
import App from './App.vue'
import Vuex from 'vuex'
import store from './store'

Vue.use(Vuex)

import VueScrollTo from 'vue-scrollto'
Vue.use(VueScrollTo, {
  duration: 100,
  easing: 'ease-out',
})

Vue.config.productionTip = false

new Vue({
  store,
  render: h => h(App)
}).$mount('#app')
