<template>
    <div class="section2" :class="{'about-active': aboutOn}">
        <img src="@/assets/img/left-arrow.svg" class="left-arrow" id="left-arrow-about" @click="arrowFunc()" alt="">
        <div class="main" id="about-main">
            <img src="@/assets/img/about-fondo.jpg" class="about-img" alt="">
            <div class="title-about">
                <h1>About Me</h1>
                <div class="separador">
                    <div class="linea1"></div>
                    <div class="linea2"></div>
                </div>
                <h3>I love Technology, Design,<br/>and Learn.</h3>
                <a class="btn" @click.prevent="aboutToggle()" v-if="!aboutOn">Show me more</a>
                <a v-scroll-to="'#aboutSecondary'" class="btn" v-if="aboutOn">Scroll down</a>
            </div>
        </div>
        <img src="@/assets/img/down-arrow.svg" class="down-arrow" v-scroll-to="'#aboutSecondary'" id="down-arrow-about" v-if="aboutOn" alt="">
        <div class="secondary" id="aboutSecondary" v-if="aboutOn">
            <AboutMe/>
            <Skills/>
            <AboutFoot/>
        </div>
    </div>
</template>

<style lang="scss">
    .section2 {
        transition: all 0.4s ease-in-out;
        .left-arrow {
            opacity: 0;
            visibility: hidden;
            transition: all 0.2s ease-in-out;
            height: 60px;
            width: auto;
            position: fixed;
            top: 2%;
            left: 0%;
            cursor: pointer;
            z-index: 99999999999;
            transform: translateX(-100%);
        }
        .down-arrow {
            opacity: 0;
            visibility: hidden;
            transition: all 0.4s ease-in-out;
            height: auto;
            width: 60px;
            position: fixed;
            bottom: 2%;
            left: 50%;
            transform: translateX(-50%);
            cursor: pointer;
            z-index: 99999999999;
            animation-name: arrowMove;
            animation-duration: 1.5s;
            animation-iteration-count: infinite;
            animation-timing-function: ease-in-out;
        }
        .main {
            height: 100vh;
            width: 100vw;
            position: relative;
        }
        .title-about {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 11%;
            z-index: 40;
            transition: all 0.6s ease-in-out;
            h1 {
                font-weight: 400;
                font-size: 120px;
                line-height: 120px;
                letter-spacing: 0.1em;
                color: #ffffff;
                font-family: 'Source Sans Pro', sans-serif;
                opacity: 0;
                transition: all 0.6s ease-in-out;
                text-shadow: 0 10px 30px rgb(2 11 22 / 50%);
            }
            .separador {
                width: 100%;
                margin-top: 30px;
                // opacity: 0;
                transition: all 0.6s ease-in-out;
                .linea1,.linea2 {
                    width: 88px;
                    height: 4px;
                    background: #ff4d5a;
                    border-radius: 5px;
                }
                .linea2 {
                    margin-top: 13px;
                    margin-left: 60px;
                    margin-bottom: 30px;
                }
            }
            h3 {
                font-weight: 400;
                font-size: 26px;
                letter-spacing: 0.1em;
                line-height: 42px;
                color: #ffffff;
                font-family: 'Source Sans Pro', sans-serif;
                opacity: 0;
                transition: all 0.6s ease-in-out;
                margin-bottom: 60px;
            }
            .btn {
                font-size: 1.4rem;
                color: #fff;
                background: #ff4d5a;
                font-family: 'Source Sans Pro', sans-serif;
                font-weight: 400;
                line-height: 1;
                padding: 18px 32px;
                border-radius: 50px;
                letter-spacing: 0.1em;
                outline: none;
                border: none;
                cursor: pointer;
                opacity: 0;
                transition: all 0.6s ease-in-out;
                text-decoration: none;
            }
        }
        .about-img {
            position: absolute;
            right: -10%;
            top: 50%;
            transform: translateY(-50%);
            width: 53.125%;
            height: 74.81%;
            opacity: 0;
            transition: all 0.7s ease-in-out;
            transition-delay: 0s;
            z-index: 30;
        }
    }
    .active {
        .title-about {
            h1,h3,.separador,.btn{
                opacity: 1 !important;
                transition: all 0.5s ease-in-out !important;
                transition-delay: 0s !important;
            }
        } 
        .about-img {
            right: 5% !important;
            opacity: 0.9 !important;
            transition: all 0.4s ease-in-out !important;
        }
    }
    .about-active {
        // background: linear-gradient(to right,#1a5061,#242958,#242958);
        .left-arrow, .down-arrow {
            opacity: 1;
            visibility: visible;
        }
        .left-arrow {
            left: 2% !important;
            transform: translateX(0) !important;
        }
        .about-img {
            right: 0 !important;
            height: 100vh;
            width: 60%;
            opacity: 0.4 !important;
        }
        .secondary {
            min-height: 100vh;
            background: white;
        }
    }
    @keyframes arrowMove {
        0%   {bottom: 2%;}
        25%  {bottom: 3%;}
        50%  {bottom: 2%;}
        75%  {bottom: 3%;}
        100% {bottom: 2%;}
    }
</style>

<script>
import {mapState,mapMutations} from 'vuex';
import AboutMe from '@/components/about/AboutMe.vue'
import Skills from '@/components/about/Skills.vue'
import AboutFoot from '@/components/about/AboutFoot.vue'
export default {
    name:'Section2',
    components: {
        AboutMe,
        Skills,
        AboutFoot
    },
    computed: {
        ...mapState(['aboutOn'])
    },
    methods: {
        ...mapMutations(['aboutToggle','aboutOff']),
        arrowFunc: function() {
            if (window.scrollY == 0) {
                this.aboutOff();
            }
            else {
                window.scrollTo(0,0);
                const checkIfScrollToIsFinished = setInterval(() => {
                    if (window.scrollY === 0) {
                        clearInterval(checkIfScrollToIsFinished);
                        setTimeout(() => {
                            this.aboutOff();
                        }, 100);
                    }
                }, 25);
            }
        }
    },
    mounted() {
        document.addEventListener('scroll',()=>{
            const secondary = document.querySelector('#aboutSecondary');
            const arrow = document.querySelector('#left-arrow-about');
            const arrowDown = document.querySelector('#down-arrow-about');
            if (secondary != null) {
                if ( (secondary.offsetTop /2) <= window.scrollY) {
                    arrow.style.filter = 'invert(1)'
                }
                else {
                    arrow.style.filter = 'invert(0)'
                }
                if ( window.scrollY > 50 ) {
                    arrowDown.style.opacity = 0;
                    arrowDown.style.visibility = 'hidden';
                }
                else {
                    arrowDown.style.opacity = 1;
                    arrowDown.style.visibility = 'visible';
                }
            }
        })
    }
}
</script>