<template>
    <div id="aboutMe">
        <p class="number">01</p>
        <h2 class="aboutMe-title">WHO I AM</h2>
        <div class="aboutMe-description">
            <h3>EMENS FEDERICO</h3>
            <p>Born in 1995 in Campana, Bs As. I am a student of information systems engineering at the national technological university, in Concepcion Del Uruguay. I have one year of experience working as a front end developer doing from web pages to more complex applications.<br/>
            I love the design of clean interfaces, achieving a good user experience and especially applications that are useful to people.</p>
            <img src="@/assets/img/yo.jpg" alt="">
        </div>
