</template>

<script>
export default {
    name: 'AboutMe',
    mounted() {
        const about = document.querySelector('#aboutMe');
        document.addEventListener('scroll',()=>{
            if(about != null) {
                let porcentaje = (about.scrollHeight * 140) / 100;
                if ( about.offsetTop <= (window.scrollY + porcentaje)) {
                    about.classList.add('reveal');
                }
            }
        })
    },
}
</script>

<style lang="scss">
    #aboutMe {
        min-height: 100vh;
        width: 100%;
