            padding: 0 4%;
            font-size: 45px;
            color: #ff4d5a;
            font-family: 'Abril Fatface', cursive;
            line-height: 50px;
            position: relative;
            opacity: 0;
            transform: translateX(-80px);
            transition: all 1s;
        }
        .number::after {
            content: "";
            width: 3.8vw;
            height: 2px;
            background: #020b16;
            position: absolute;
            top: 25px;
            transform: translateY(-50%);
            left: 0;
            margin: auto;
        }
        .aboutMe-title {
            width: 40%;
            padding: 0 2%;
            font-size: 45px;
            color: #072142;
