        <img class="img-back-1" src="../assets/img/fondo.svg" alt="">
        <p class="scrolldown">SCROLLDOWN</p>
    </div>
</template>

<style lang="scss">
    #section1 {
        height: 100vh;
        width: 100vw;
        position: relative;
        overflow: hidden;
        .presentation {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 11%;
            z-index: 90;
            h1 {
                font-weight: 400;
                font-size: 120px;
                line-height: 120px;
