                color: #ffffff;
                font-family: 'Source Sans Pro', sans-serif;
                opacity: 0;
                transition: all 0.4s;
            }
            .separador {
                width: 100%;
                margin-top: 30px;
                margin-bottom: 30px;
                opacity: 0;
                transition: all 0.4s;
                .linea1,.linea2 {
                    width: 88px;
                    height: 4px;
                    background: #ff4d5a;
                    border-radius: 5px;
                }
                .linea2 {
                    margin-top: 13px;
                    margin-left: 60px;
                }
            }
            h3 {
                font-weight: 400;
                font-size: 26px;
                letter-spacing: 0.1em;
                line-height: 42px;
                color: #ffffff;
                font-family: 'Source Sans Pro', sans-serif;
                opacity: 0;
                transition: all 0.4s;
            }
        }
        .img-back-1,.img-back-2 {
            position: absolute;
            right: -5%;
            top: 7%;
            height: 86%;
            max-width: 58vw;
            opacity: 0;
            transition: all 0.5s;
            z-index: 30;
        }
        .img-back-2 {
            right: -4%;
            top: 5%;
            opacity: 0;
        }
        .scrolldown {
            font-size: 0.9rem;
            line-height: 1;
            letter-spacing: 4px;
            color: #ffffff;
            font-family: 'Source Sans Pro', sans-serif;
            opacity: 1;
            writing-mode: vertical-rl;
            position: absolute;
            bottom: 0px;
            transform: translateY(100%);
            left: 40px;
            transition: all 0.4s;
        }
        .scrolldown::after {
            content: "";
            width: 2px;
            height: 64px;
            background: #fff;
            position: absolute;
            left: 0;
            right: 0;
            margin: auto;
            bottom: -80px;
        }
    }
    .active {
        .presentation {
            h1,h3,.separador{
                opacity: 1 !important;
                transition: all 0.5s ease-in-out !important;
                transition-delay: 0.8s !important;
            }
        }
        .img-back-1,.img-back-2 {
            right: 5% !important;
            opacity: 1 !important;
            // transition: all 0.4s ease-in-out !important;
            // transition-delay: 0.3s !important;
            transition: opacity 0.4s 0.7s, right 0.4s 0.7s, transform 0s 0s !important;
        }
        .img-back-2 {
